.profile__description {
    color: #fff;
    font-family: 'Inter', Arial, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    margin: 0;
    max-width: 336px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    padding: 0;
}
     
@media screen and (max-width: 1024px) {
    .profile__description {
       font-size: 14px;
       line-height: 17px;
       max-width: 282px;
       text-align: center;
    }
}