.popup__close-button {
    background: url(../../../images/close.svg);
    width: 32px;
    height: 32px;
    border: none;
    cursor: pointer;
    padding: 0;
    position: absolute;
    right: -8px;
    top: -8px;
    transform: translate(100%, -100%);
    transition: opacity 0.2s ease-in-out;
}
    
.popup__close-button:hover {
    opacity: 0.6;
}
    
@media screen and (max-width: 768px) {
    .popup__close-button {
      top: -16px;
      right: 19px;
      width: 20px;
      height: 20px;
    }
}