.profile {
    max-width: 880px;
    width: 100%;
    padding-top: 40px;
    padding-bottom: 51px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    justify-content: space-between;
}

@media screen and (max-width: 1024px) {
    .profile {
      padding: 42px 19px 36px;
      flex-direction: column;
      justify-content: flex-start;
      gap: 36px;
    }
}