.popup__container {
    padding: 34px 36px 36px;
    box-sizing: border-box;
    background: #FFFFFF;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    max-width: 430px;
    width: 100%;
    position: relative;

}
  
@media screen and (max-width: 768px) {
    .popup__container {
      padding: 25px 22px;
      max-width: 282px;


    }
}